.footer-container {
    // background-color: #fff;
    padding: 2rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
}

.footer-link-wrapper {
    display: flex;
    
}

.footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
}

.footer-link-items h2 {
    margin-bottom: 16px;

}

.footer-link-items >h2 {
    color: #fff;

}
.footer-link-items a{
    color: #fff;
    text-decoration: none;
    margin-bottom: 8px;
}

.footer-link-items a:hover{
    color: #fff;
    transition: 0.3s ease-out;
}

.footer-email-form h2 {
    margin-bottom: 32px;
}

.footer-input::placeholder {
    color:#fff;

}

/* Social Icons */
.social-icon-link {
    color :#fff;
    font-size: 24px;

}

.social-media {
    max-width: 1000px;
    width: 100%;
}

.social-media-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin:40px auto 0 auto;


}

// .social-icons {
//     display: flex;
//     justify-content: space-between;
//     align-items: center;
//     width: 240px;
// }

.social-logo {
    color:#fff;
    justify-self:center;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    align-self: center;
    margin-bottom: 16px;
}

@media screen and (max-width:820px) {
    .footer-links {
        padding-top: 2rem;

    }

    .footer-input {
        width: 100%;

    }
    .btn {
        width: 100%;
    }
    .footer-link-wrapper {
        flex-direction: column;
    }

    .social-media-wrap {
        flex-direction: column;

    }
}